var loginRes, _self;
export default {
	data() {
		return {
			myFace : '',
			user:{}
		}
	},
	onLoad:function(){
		_self = this;
		loginRes = this.checkLogin('../tabbar-5/tabbar-5', '2');
		if(!loginRes){return ;}
		this.myFace = loginRes[3];
	},
	onShow : function() {
		loginRes = this.checkLogin('../tabbar-5/tabbar-5', '2');
		if(!loginRes){return false;}
		// 加载用户信息
	    uni.request({
	        url: this.apiServer + 'my&m=info',
	        method: 'POST',
	        header: {'content-type' : "application/x-www-form-urlencoded"},
	        data: {
	            uid    : loginRes[0],
	            random : loginRes[1]
	        },
	        success: res => {
	            console.log(JSON.stringify(res));
	            if(res.data.status == 'ok'){
	                this.user = res.data.data;
	            }
	        }
	    });
	},
	methods: {
		goToPage(url) {
			if (!url) return;
			uni.navigateTo({
				url
			});
		},
		logoff : function(){
			uni.removeStorageSync('SUID');
			uni.removeStorageSync('SRAND');
			uni.showToast({
				title:"您已经退出",
				icon: "none"
			});
			setTimeout(function(){
				uni.switchTab({
					url:'../tabbar-1/tabbar-1'
				});
			}, 1000);
		}
	}
}

